form {
  text-align: center;
  /* width: 90%; */
  /* margin: 0 auto; */
}

input[type='text'] {
  border: 1px solid black;
  width: 50%;
  padding: 2px 12px;
  border-radius: 24px;
  margin: 5px 0px 5px 0px;
}
input[type='submit'] {
  padding: 12px 24px;
  background-color: #008a00;
  color: #ffffff;
  width: 50%;
  text-align: center;
  margin: 0 auto;
  border-radius: 24px;
  font-size: 16px;
  font-weight: 500;
}
.errorMessage {
  color: red;
  margin: 5px 0 5px 0;
}

.name {
  text-align: center;
}

.returnBy {
  text-align: center;
}
